import React, { useState, useEffect } from 'react';
import toast from 'react-hot-toast';
import { useSocket } from '../contexts/SocketContext';
import './Controls.css';

const Controls = () => {
  const [devices, setDevices] = useState([]);
  const [selectedDevice, setSelectedDevice] = useState(null);
  const [controls, setControls] = useState([]);
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const { connected, sendControlCommand } = useSocket();
  
  const [newControl, setNewControl] = useState({
    controlType: 'light',
    mode: 'manual',
    manualSettings: { intensity: 50, duration: 0 },
    scheduledSettings: { 
      schedule: [{ 
        days: ['monday'], 
        startTime: '09:00', 
        endTime: '18:00', 
        intensity: 50 
      }] 
    },
    autoSettings: { targetValue: 50, tolerance: 5 },
    thresholdSettings: { 
      condition: 'above', 
      value: 80, 
      action: 'activate', 
      intensity: 100 
    }
  });

  useEffect(() => {
    fetchDevices();
  }, []);

  useEffect(() => {
    if (selectedDevice) {
      fetchControls(selectedDevice._id);
    }
  }, [selectedDevice]);

  const fetchDevices = async () => {
    try {
      const response = await fetch('/api/devices/my-devices', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setDevices(data.devices || []);
        if (data.devices?.length > 0) {
          setSelectedDevice(data.devices[0]);
        }
      }
    } catch (error) {
      console.error('Failed to fetch devices:', error);
      toast.error('Li kt ni server');
    } finally {
      setLoading(false);
    }
  };

  const fetchControls = async (deviceId) => {
    try {
      const response = await fetch(`/api/controls/device/${deviceId}`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setControls(data || []);
      }
    } catch (error) {
      console.error('Failed to fetch controls:', error);
    }
  };

  const handleCreateControl = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch('/api/controls', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({
          deviceId: selectedDevice._id,
          ...newControl
        })
      });

      if (response.ok) {
        setOpenDialog(false);
        fetchControls(selectedDevice._id);
        toast.success('To iu khin thnh cng!');
        setNewControl({
          controlType: 'light',
          mode: 'manual',
          manualSettings: { intensity: 50, duration: 0 },
          scheduledSettings: { 
            schedule: [{ 
              days: ['monday'], 
              startTime: '09:00', 
              endTime: '18:00', 
              intensity: 50 
            }] 
          },
          autoSettings: { targetValue: 50, tolerance: 5 },
          thresholdSettings: { 
            condition: 'above', 
            value: 80, 
            action: 'activate', 
            intensity: 100 
          }
        });
      } else {
        const error = await response.json();
        toast.error(error.message || 'To iu khin tht bi');
      }
    } catch (error) {
      console.error('Create control error:', error);
      toast.error('Li kt ni server');
    }
  };

  const handleControlAction = async (control, action) => {
    try {
      const response = await fetch(`/api/controls/${control._id}/${action}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({
          intensity: control.manualSettings?.intensity || 50,
          duration: control.manualSettings?.duration || 0
        })
      });

      if (response.ok) {
        // Send real-time command via WebSocket
        sendControlCommand(
          selectedDevice._id,
          control.controlType,
          action,
          {
            intensity: control.manualSettings?.intensity || 50,
            duration: control.manualSettings?.duration || 0
          }
        );
        
        fetchControls(selectedDevice._id);
        toast.success(`${action === 'activate' ? 'Bt' : 'Tt'} iu khin thnh cng!`);
      } else {
        const error = await response.json();
        toast.error(error.message || `${action} tht bi`);
      }
    } catch (error) {
      console.error(`Control ${action} error:`, error);
      toast.error('Li kt ni server');
    }
  };

  const getControlIcon = (type) => {
    switch (type) {
      case 'light': return 'light_mode';
      case 'water': return 'water_drop';
      case 'fan': return 'air';
      case 'heater': return 'local_fire_department';
      case 'cooler': return 'ac_unit';
      default: return 'settings';
    }
  };

  const getModeIcon = (mode) => {
    switch (mode) {
      case 'manual': return 'pan_tool';
      case 'scheduled': return 'schedule';
      case 'auto': return 'smart_toy';
      case 'threshold': return 'warning';
      default: return 'settings';
    }
  };

  const getModeDescription = (control) => {
    switch (control.mode) {
      case 'manual':
        return `Cng : ${control.manualSettings?.intensity || 0}% | Thi gian: ${control.manualSettings?.duration || 0} pht`;
      case 'auto':
        return `Mc tiu: ${control.autoSettings?.targetValue || 0}  ${control.autoSettings?.tolerance || 0}`;
      case 'threshold':
        return `${control.thresholdSettings?.condition === 'above' ? 'Trn' : 'Di'} ${control.thresholdSettings?.value || 0}`;
      case 'scheduled':
        return 'Theo lch  t';
      default:
        return 'Cha cu hnh';
    }
  };

  const getControlColor = (type) => {
    switch (type) {
      case 'light': return 'yellow';
      case 'water': return 'blue';
      case 'fan': return 'cyan';
      case 'heater': return 'red';
      case 'cooler': return 'blue';
      default: return 'gray';
    }
  };

  if (loading) {
    return (
      <div className="controls-loading">
        <div className="loading-spinner"></div>
      </div>
    );
  }

  if (devices.length === 0) {
    return (
      <div className="controls-container">
        <div className="controls-header">
          <div className="header-text">
            <h2 className="controls-title">iu khin thit b</h2>
            <p className="controls-subtitle">Qun l v iu khin cc thit b IoT</p>
          </div>
        </div>
        <div className="warning-card">
          <div className="warning-icon">
            <span className="material-symbols-outlined">warning</span>
          </div>
          <div className="warning-content">
            <h3>Cha c thit b</h3>
            <p>Bn cha c thit b no c gn. Lin h Manager  c cp thit b ESP32.</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="controls-container">
      {/* Header */}
      <div className="controls-header">
        <div className="header-text">
          <h2 className="controls-title">iu khin thit b</h2>
          <p className="controls-subtitle">Qun l v iu khin cc thit b IoT</p>
        </div>
        <div className="header-actions">
          <div className="device-selector">
            <label className="selector-label">Chn thit b:</label>
            <select
              className="device-select"
              value={selectedDevice?._id || ''}
              onChange={(e) => {
                const device = devices.find(d => d._id === e.target.value);
                setSelectedDevice(device);
              }}
            >
              {devices.map(device => (
                <option key={device._id} value={device._id}>
                  {device.name} ({device.deviceId})
                </option>
              ))}
            </select>
          </div>
          <button
            className="add-control-button"
            onClick={() => setOpenDialog(true)}
          >
            <span className="material-symbols-outlined">add</span>
            Thm iu khin
          </button>
        </div>
      </div>

      {/* Device Status */}
      {selectedDevice && (
        <div className="device-status-card">
          <div className="device-status-header">
            <div className="device-info">
              <h3 className="device-name">{selectedDevice.name}</h3>
              <p className="device-id">ID: {selectedDevice.deviceId}</p>
            </div>
            <div className="device-badges">
              <span className={`device-badge ${selectedDevice.status}`}>
                <span className="material-symbols-outlined">
                  {selectedDevice.status === 'online' ? 'wifi' : 
                   selectedDevice.status === 'offline' ? 'wifi_off' : 'build'}
                </span>
                {selectedDevice.status === 'online' ? 'Trc tuyn' : 
                 selectedDevice.status === 'offline' ? 'Ngoi tuyn' : 'Bo tr'}
              </span>
              <span className={`connection-badge ${connected ? 'connected' : 'disconnected'}`}>
                <span className="material-symbols-outlined">
                  {connected ? 'sync' : 'sync_disabled'}
                </span>
                {connected ? 'Real-time' : 'Offline'}
              </span>
            </div>
          </div>
        </div>
      )}

      {/* Controls Grid */}
      {controls.length === 0 ? (
        <div className="empty-controls">
          <div className="empty-controls-icon">
            <span className="material-symbols-outlined">settings</span>
          </div>
          <div className="empty-controls-content">
            <h3>Cha c iu khin no</h3>
            <p>Hy thm iu khin u tin cho thit b ny!</p>
            <button
              className="add-control-button"
              onClick={() => setOpenDialog(true)}
            >
              <span className="material-symbols-outlined">add</span>
              Thm iu khin
            </button>
          </div>
        </div>
      ) : (
        <div className="controls-grid">
          {controls.map(control => (
            <ControlCard 
              key={control._id} 
              control={control} 
              onAction={handleControlAction}
              getControlIcon={getControlIcon}
              getModeIcon={getModeIcon}
              getModeDescription={getModeDescription}
              getControlColor={getControlColor}
            />
          ))}
        </div>
      )}

      {/* Add Control Dialog */}
      {openDialog && (
        <div className="dialog-overlay" onClick={() => setOpenDialog(false)}>
          <div className="dialog-content" onClick={(e) => e.stopPropagation()}>
            <div className="dialog-header">
              <h3 className="dialog-title">To iu khin mi</h3>
              <button 
                className="dialog-close"
                onClick={() => setOpenDialog(false)}
              >
                <span className="material-symbols-outlined">close</span>
              </button>
            </div>
            
            <form onSubmit={handleCreateControl} className="control-form">
              <div className="form-grid">
                <div className="form-group">
                  <label className="form-label">Loi iu khin</label>
                  <select
                    className="form-select"
                    value={newControl.controlType}
                    onChange={(e) => setNewControl({...newControl, controlType: e.target.value})}
                  >
                    <option value="light"> n</option>
                    <option value="water"> Ti nc</option>
                    <option value="fan"> Qut</option>
                    <option value="heater"> Si m</option>
                    <option value="cooler"> Lm mt</option>
                  </select>
                </div>
                
                <div className="form-group">
                  <label className="form-label">Ch </label>
                  <select
                    className="form-select"
                    value={newControl.mode}
                    onChange={(e) => setNewControl({...newControl, mode: e.target.value})}
                  >
                    <option value="manual"> Th cng</option>
                    <option value="scheduled"> Hn gi</option>
                    <option value="auto"> T ng</option>
                    <option value="threshold"> Ngng</option>
                  </select>
                </div>
              </div>

              {/* Manual Settings */}
              {newControl.mode === 'manual' && (
                <div className="mode-settings">
                  <h4 className="settings-title">Ci t th cng</h4>
                  <div className="form-group">
                    <label className="form-label">
                      Cng : {newControl.manualSettings.intensity}%
                    </label>
                    <input
                      type="range"
                      className="range-slider"
                      min="0"
                      max="100"
                      value={newControl.manualSettings.intensity}
                      onChange={(e) => setNewControl({
                        ...newControl,
                        manualSettings: { ...newControl.manualSettings, intensity: parseInt(e.target.value) }
                      })}
                    />
                  </div>
                  <div className="form-group">
                    <label className="form-label">Thi gian (pht, 0 = khng gii hn)</label>
                    <input
                      type="number"
                      className="form-input"
                      min="0"
                      value={newControl.manualSettings.duration}
                      onChange={(e) => setNewControl({
                        ...newControl,
                        manualSettings: { ...newControl.manualSettings, duration: parseInt(e.target.value) || 0 }
                      })}
                    />
                  </div>
                </div>
              )}

              {/* Auto Settings */}
              {newControl.mode === 'auto' && (
                <div className="mode-settings">
                  <h4 className="settings-title">Ci t t ng</h4>
                  <div className="form-grid">
                    <div className="form-group">
                      <label className="form-label">Gi tr mc tiu</label>
                      <input
                        type="number"
                        className="form-input"
                        value={newControl.autoSettings.targetValue}
                        onChange={(e) => setNewControl({
                          ...newControl,
                          autoSettings: { ...newControl.autoSettings, targetValue: parseInt(e.target.value) || 0 }
                        })}
                      />
                    </div>
                    <div className="form-group">
                      <label className="form-label">Dung sai ()</label>
                      <input
                        type="number"
                        className="form-input"
                        value={newControl.autoSettings.tolerance}
                        onChange={(e) => setNewControl({
                          ...newControl,
                          autoSettings: { ...newControl.autoSettings, tolerance: parseInt(e.target.value) || 0 }
                        })}
                      />
                    </div>
                  </div>
                </div>
              )}

              {/* Threshold Settings */}
              {newControl.mode === 'threshold' && (
                <div className="mode-settings">
                  <h4 className="settings-title">Ci t ngng</h4>
                  <div className="form-grid">
                    <div className="form-group">
                      <label className="form-label">iu kin</label>
                      <select
                        className="form-select"
                        value={newControl.thresholdSettings.condition}
                        onChange={(e) => setNewControl({
                          ...newControl,
                          thresholdSettings: { ...newControl.thresholdSettings, condition: e.target.value }
                        })}
                      >
                        <option value="above">Trn</option>
                        <option value="below">Di</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label className="form-label">Gi tr ngng</label>
                      <input
                        type="number"
                        className="form-input"
                        value={newControl.thresholdSettings.value}
                        onChange={(e) => setNewControl({
                          ...newControl,
                          thresholdSettings: { ...newControl.thresholdSettings, value: parseInt(e.target.value) || 0 }
                        })}
                      />
                    </div>
                  </div>
                </div>
              )}
              
              <div className="dialog-actions">
                <button 
                  type="button" 
                  className="btn-secondary"
                  onClick={() => setOpenDialog(false)}
                >
                  Hy
                </button>
                <button type="submit" className="btn-primary">
                  To iu khin
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

// Control Card Component
const ControlCard = ({ control, onAction, getControlIcon, getModeIcon, getModeDescription, getControlColor }) => {
  const color = getControlColor(control.controlType);
  
  return (
    <div className="control-card">
      <div className="control-card-header">
        <div className="control-card-icon-wrapper">
          <div className={`control-card-icon ${color}`}>
            <span className="material-symbols-outlined icon-fill">
              {getControlIcon(control.controlType)}
            </span>
          </div>
          <div className="control-card-info">
            <h4 className="control-card-name">
              {control.controlType.toUpperCase()}
            </h4>
            <p className="control-card-mode">
              <span className="material-symbols-outlined mode-icon">
                {getModeIcon(control.mode)}
              </span>
              {control.mode.toUpperCase()}
            </p>
          </div>
        </div>
        <span className={`control-status-badge ${control.status}`}>
          {control.status === 'active' ? 'ang hot ng' : 'Tt'}
        </span>
      </div>

      <div className="control-card-description">
        <p className="control-description-text">
          {getModeDescription(control)}
        </p>
      </div>

      {control.currentState && (
        <div className="control-card-state">
          <div className="state-item">
            <span className="state-label">Cng  hin ti:</span>
            <span className="state-value">{control.currentState.intensity || 0}%</span>
          </div>
          {control.currentState.lastActivated && (
            <div className="state-item">
              <span className="state-label">Ln cui:</span>
              <span className="state-value">
                {new Date(control.currentState.lastActivated).toLocaleString('vi-VN')}
              </span>
            </div>
          )}
        </div>
      )}

      <div className="control-card-actions">
        {control.status === 'active' ? (
          <button
            className="control-action-button stop"
            onClick={() => onAction(control, 'deactivate')}
          >
            <span className="material-symbols-outlined">stop</span>
            Tt
          </button>
        ) : (
          <button
            className="control-action-button start"
            onClick={() => onAction(control, 'activate')}
          >
            <span className="material-symbols-outlined">play_arrow</span>
            Bt
          </button>
        )}
      </div>
    </div>
  );
};

export default Controls;